.heroContainer {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    color: #fff;
}

.hero {
    height: 100vh;
    display: flex;
    align-items: center;
    background-color: #414141;
}

.presentation {
    width: 198px;
    height: 190px;
    margin: 0 0 0 50px;
    line-height: 37px;
    transform: translateY(40px);
}

.photoWrapper {
    width: 300px;
    height: 300px;
    border-radius: 50%;
    overflow: hidden;
    animation: photoLGMedia 800ms ease-in;
}

.photo {
    height: 100%;
    transform: translateX(-12px);
}

.span {
    display: block;
}

@keyframes photoLGMedia {
    from {
        transform: translateX(-10px);
        opacity: 0.5;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes photoSMMedia {
    from {
        transform: translateY(-10px);
        opacity: 0.5;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}


@media (max-width: 900px) {
    .heroContainer {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .photoWrapper {
        animation: photoSMMedia 800ms ease-in;
    }

    .presentation {
        margin: 50px 0 0 0;
        width: 174px;
        font-size: 28px;
        transform: translateY(0);
    }
}


