.burger {
    display: inline-flex;
    align-items: center;
    position: fixed;
    top: 40px;
    right: -15px;
    height: 60px;
    width: 80px;
    padding-left: 6px;
    background: linear-gradient(90deg, #414141 15%, #343434 91.4%);
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    cursor: pointer;
    z-index: 10;
    transition: all 1000ms ease-in-out;
}

.burgerCircle {
    height: 50px;
    width: 50px;
    border: 6px solid #958f78;
    background-color: #fff;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.burgerActive {
    animation: burgerHide 500ms ease-in-out;
    right: -100px;
}

.menu {
    position: fixed;
    top: 40px;
    right: -500px;
    background: linear-gradient(90deg, transparent 15%, #343434 91.4%);
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    color: #fff;
    height: 60px;
    width: 430px;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    transition: all 1000ms ease;
}

.menuList {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 0 0 0 30px;
}

.menuItem {
    cursor: pointer;
    color: #aeaeae;
    margin-right: 15px;
    position: relative;
    transition: all 300ms;
}

.menuItem:after {
    content: '';
    background-color: #aeaeae;
    height: 1px;
    width: 100%;
    display: flex;
    align-self: flex-end;
    position: absolute;
    bottom: -6px;
    left: 0;
    opacity: 0;
    transition: all 700ms;
}

.menuItem:hover.menuItem:after {
    opacity: 1;
}

.menuItem:hover {
    color: #fff;
}

.menuActive {
    right: -10px;
}

.close {
    margin-right: 25px;
    cursor: pointer;
}

@keyframes burgerHide {
    from {
        right: -10px;
    }
    50% {
        right: 0;
    }
    to {
        right: -100px;
    }
}

@keyframes menuShow {
    from {
        right: -500px;
    }
    50% {
        right: 0;
    }
    to {
        right: -10px;
    }
}
